import axios from "axios";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { getwatchliststocks, getStocksDetailsApi, addstocktowatchlist, removestockfromwatchlist, searchstockApi } from "../watchlist/watchlistApi";

function Strategies() {
  const dispatch = useDispatch();

  const [searchTerm, setSearchTerm] = React.useState("");
  const [searchResults, setSearchResults] = React.useState([]);
  const [watchList, setWatchList] = React.useState([]);
  const [strategies, setStrategies] = React.useState([]);

  const stockpricesR = useSelector((state) => state.watchlist.stockprices);  
  const searchlistR = useSelector((state) => state.watchlist.searchlist);
  const searchTermR = useSelector((state) => state.watchlist.searchterm);
  const watchlistR = useSelector((state) => state.watchlist.watchlist);

  const handleChange = (event) => {
    setSearchTerm(event.target.value);
  };

  const handleKeypress = (e) => {
    //it triggers by pressing the enter key
    if (e.keyCode === 13) {
      searchStock();
    }
  };

  const searchStock = () => {
    if (searchTerm.length == 0) {
      alert("Please enter name of stock");
      return;
    }

    let searchApi =
      "https://query2.finance.yahoo.com/v1/finance/search?q=" +
      searchTerm +
      "&quotesCount=6&newsCount=0&enableFuzzyQuery=false&quotesQueryId=tss_match_phrase_query&multiQuoteQueryId=multi_quote_single_token_query&newsQueryId=news_ss_symbols&enableCb=false&enableNavLinks=false&vespaNewsTimeoutMs=600";
    axios
      .request({
        method: "get",
        url: `https://api.allorigins.win/get?url=${encodeURIComponent(
          searchApi
        )}`,
      })
      .then((response) => {
        console.log(response);
        setSearchResults(JSON.parse(response.data.contents).quotes);
      });
  };
    
  const addStockToList = (item) => {
    axios
    .request({
      method: "post",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks",
      data: item,
    })
    .then((response) => {
      console.log(response.data);
      getAllStocks();
    });    
  };

  // const removeStockFromList = (item) => {
  //   var filtered = watchList.filter(el => { return el.symbol !== item.symbol; });
  //     setWatchList([...filtered]);
  // };

  const getAllStocks = () => {
    axios
    .request({
      method: "get",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks",
    })
    .then((response) => {
      console.log(response.data);
      setWatchList(response.data);
    });
  }

  const removeStock = (stockSymbol) => {
    axios
    .request({
      method: "post",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/stocks/delete/"+stockSymbol,
    })
    .then((response) => {
      console.log(response.data);
      getAllStocks();
    });
  }

  const getAllStrategies = () => {
    axios
    .request({
      method: "get",
      url: "https://us-central1-stockwatch-193209.cloudfunctions.net/strategy",
    })
    .then((response) => {
      console.log(response.data);
      setStrategies(response.data);
    });
  }

  useEffect(() => {
    getAllStocks();
    getAllStrategies();
  }, []);

  return (
    <div className="App">
          <div style={{display:"flex", flexDirection:"column", marginLeft:10}}>
        <input
          style={{width:"300px"}}
          type="text"
          placeholder="Strategy Name"
          value={searchTerm}
          onChange={handleChange}
          onKeyDown={handleKeypress}
        />
        <button
          style={{width:"100px", marginTop:2}}
          onClick={() => {
            searchStock();
          }}
        >
          Add Strategy
        </button>
      </div>
      <br/><br/>

       <div style={{display:"flex", flexDirection:"row"}}>
      &nbsp;&nbsp;
      <table border="1px solid black;">
        <tr><th>&nbsp;&nbsp;Strategy&nbsp;&nbsp;</th></tr>
        {strategies.length > 0 ? (
          <tr>
            <th>{"Name"}</th> 
          </tr>
        ) : null}
        {strategies.map((item) => {
          return (
            <tr>
              <td>{item.name}</td>
              <td>{<button onClick={()=>{}}>Remove</button>}</td>
              {/* <td>{JSON.stringify(item)}</td> */}
            </tr>
          );
        })}
      </table>
      </div>

      {strategies.map((item) => {
          return (
                <div style={{display:"flex", justifyContent:"center", alignItems:"center", backgroundColor: "green", width:100, height: 40, color:"white", borderRadius:30, margin:15}}>{item.name}</div>
          );
        })}


      
      <br/>
      <br/>
      
      
      <div style={{display:"flex", flexDirection:"column", width:"30%", marginLeft:10}}>
      <input
        // style={{width:"100%"}}
        type="text"
        placeholder="Search"
        value={searchTerm}
        onChange={handleChange}
        onKeyDown={handleKeypress}
      />
      <button
        style={{width:"40%", marginTop:2}}
        onClick={() => {
          searchStock();
        }}
      >
        {" "}
        Search{" "}
      </button>
      </div>
      <br/>
      <br/>

      <table border="1px solid black;">
        <th>Search List</th>
        <tr>
          <th>{"Exchange"}</th>
          <th>{"Name"}</th>
        </tr>
        {searchlistR.map((item) => {
          return (
            <tr>
              <td>{item.exchDisp}</td> <td>{item.shortname}</td>{" "}
              <td>
                {
                  <button
                    onClick={() => {
                      
                      let objP = {};
                      objP.stock = item;
                      objP.callback = () => { 
                        dispatch(getwatchliststocks((stocksData)=>{
                          dispatch(getStocksDetailsApi(stocksData));
                        }));
                      };
                      
                      dispatch(addstocktowatchlist(
                        objP
                      ));
                    }}
                  >
                    Add
                  </button>
                }
              </td>
            </tr>
          );
        })}
      </table>

      {/* {JSON.stringify()} */}
      <div style={{display:"flex", flexDirection:"row"}}>
      <table border="1px solid black;">
        <tr><th>WatchList</th></tr>
        {watchlistR.length > 0 ? (
          <tr>
            <th>{"Exchange"}</th>
            <th>{"Name"}</th> 
            <th>{"Price"}</th>
          </tr>
        ) : null}
        {watchlistR.map((item) => {
          return (
            <tr>
              <td>{item.exchDisp}</td> <td>{item.shortname}</td>{" "}
              <td>
                {stockpricesR[item.symbol]}
                {"  "}
              </td>
              <td>{<button onClick={()=>removeStock(item.symbol)}>Remove</button>}</td>
              {/* <td>{JSON.stringify(item)}</td> */}
            </tr>
          );
        })}
      </table>
      </div>

    </div>
  );
}

export default Strategies;
