import { createSlice } from '@reduxjs/toolkit'
import { getwatchliststocks, getStocksDetailsApi, addstocktowatchlist, removestockfromwatchlist, searchstockApi } from "./watchlistApi";

const initialState = {
  stockprices: {},
  searchterm: "",
  searchlist: [],
  watchlist: [],
}

export const watchlistslice = createSlice({
  name: 'watchlist',
  initialState,
  reducers: {
    setsearchtermR: (state, action) => {
      state.searchterm = action.payload;
    }
  },
    extraReducers: (builder) => {
    // Add reducers for additional action types here, and handle loading state as needed
    builder.addCase(getwatchliststocks.fulfilled, (state, action) => {
      state.watchlist = action.payload
    });
    builder.addCase(getStocksDetailsApi.fulfilled, (state, action) => {
      state.stockprices = action.payload;
    });
    builder.addCase(addstocktowatchlist.fulfilled, (state, action) => {
      //do nothing
    });
    builder.addCase(removestockfromwatchlist.fulfilled, (state, action) => {
      //do nothing
    });
    builder.addCase(searchstockApi.fulfilled, (state, action) => {
      // console.log("Success Action: ", action.payload);
      //do nothing
      state.searchlist = action.payload
    });    
  },
})

// Action creators are generated for each case reducer function
export const { setsearchtermR } = watchlistslice.actions

export default watchlistslice.reducer