import { createAsyncThunk } from '@reduxjs/toolkit'
import axios from "axios";

export const getwatchliststocks = createAsyncThunk(
  'fetchByIdStatus',
  async (callback, thunkAPI) => {
    const response = await axios.get('https://us-central1-stockwatch-193209.cloudfunctions.net/stocks');  
    callback(response.data);
    return response.data;
  }
)

const createStockPriceMap = (stocksData) => {
    return stocksData.reduce((result, item) => {
      return { [item.symbol]: item.response[0].meta.regularMarketPrice, ...result };
    }, {});
  }

export const getStocksDetailsApi = createAsyncThunk(
    'getStocksDetailsApi',
    async (stocks, thunkAPI) => {

        let stockSymbList = stocks.reduce(function (result, item) {
            if(result == "")
            return item.symbol;
            return result + "," + item.symbol;
          }, "");
      
          let searchApi = "https://query1.finance.yahoo.com/v7/finance/spark?symbols=" + stockSymbList + "&range=1d&interval=5m&indicators=close&includeTimestamps=false&includePrePost=false&corsDomain=in.finance.yahoo.com&.tsrc=finance";
      
          let body = {
            "url": searchApi
          };

        let response = await axios.post(`https://us-central1-stockwatch-193209.cloudfunctions.net/getstocks`, body);
        let priceMap = createStockPriceMap(response.data.spark.result);

        return priceMap;
    }
  )

  export const addstocktowatchlist = createAsyncThunk(
    'addstocktowatchlist',
    async ({stock, callback}, thunkAPI) => {
        const response = await axios.post('https://us-central1-stockwatch-193209.cloudfunctions.net/stocks', stock);  
        callback();
        return response.status;
    }
  )

  export const removestockfromwatchlist = createAsyncThunk(
    'removestockfromwatchlist',
    async ({stockSymbol, callback}, thunkAPI) => {
        const response = await axios.post("https://us-central1-stockwatch-193209.cloudfunctions.net/stocks/delete/"+stockSymbol);  
        callback();
        return response.status;
    }
  )

  export const searchstockApi = createAsyncThunk(
    'searchstockApi',
    async ({searchTermR, callback}, thunkAPI) => {
        let searchApi =
        "https://query2.finance.yahoo.com/v1/finance/search?q=" +
        searchTermR +
        "&quotesCount=6&newsCount=0&enableFuzzyQuery=false&quotesQueryId=tss_match_phrase_query&multiQuoteQueryId=multi_quote_single_token_query&newsQueryId=news_ss_symbols&enableCb=false&enableNavLinks=false&vespaNewsTimeoutMs=600";
  
        let body = {
            "url": searchApi
        };

        const response = await axios.post("https://us-central1-stockwatch-193209.cloudfunctions.net/getstocks", body);  
        callback();
        return response.data.quotes;
    }
  )